:root {
  // Colors
  --color-black: #1A1A1A;
  --color-black_50: rgba(0, 0, 0, .5);
  --color-black_60: rgba(26, 26, 26, .6);
  --color-black_70: rgba(26, 26, 26, .7);
  --color-gray1: #4F4F4F;
  --color-gray2: #828282;
  --color-gray2_50: rgba(130, 130, 130, .5);
  --color-gray2_70: rgba(130, 130, 130, .7);
  --color-gray3: #D0D0D0;
  --color-gray3_20: rgba(208, 208, 208, .2);
  --color-gray4: #F8F8F8;
  --color-gray4_40: rgba(248, 248, 248, .4);
  --color-gray4_80: rgb(248, 248, 248, .8);
  --color-gray5: #E0E0E0;
  --color-gray6: #BDBDBD;
  --color-gray7: #F6F6F6;
  --color-gray8: #FAFAFA;
  --color-gray9: #F7F7F7;
  --color-gray10: #FCFCFC;
  --color-white: #FFFFFF;
  --color-blue: #537293;
  --color-blue_10: rgba(83, 114, 147, .1);
  --color-red: #DA5454;
  --color-orange: #D79533;
  --color-blue-plus: #5199C1;
  --color-green-plus: #51ADC1;
  --color-green_10: rgba(81, 173, 193, .1);
  --color-button: #1D2E42;
  --color-button-hover: #294260;
  --color-filter: #3C656D;
  --color-filter-hover: #447C87;
  --color-additional-text: #537293;
  --color-additional-text2: #edf1f4;
  --color-card-plus: #F6FAFB;
  --color-screen-swiper: #374F68;
  --color-footer: #1B1F22;
  --color-transparent: rgba(255, 255, 255, 0)
}
// Fonts
@mixin font-h1 {
  font-weight: 600;
  font-size: 50px;
  line-height: 140%;
  letter-spacing: 2px;
  text-transform: uppercase;
}
@mixin font-h2 {
  font-weight: 500;
  font-size: 36px;
  line-height: 110%;
  letter-spacing: 2px;
  text-transform: uppercase;
}
@mixin font-h3 {
  font-weight: 500;
  font-size: 25px;
  line-height: 120%;
  letter-spacing: 1px;
  text-transform: uppercase;
}
@mixin font-h4 {
  font-weight: 500;
  font-size: 21px;
  line-height: 140%;
  text-decoration-line: underline;
}
@mixin font-h5 {
  font-weight: 500;
  font-size: 14px;
  line-height: 122%;
  letter-spacing: 1px;
  text-transform: uppercase;
}
@mixin font-text1 {
  font-weight: 500;
  font-size: 32px;
  line-height: 140%;
}
@mixin font-text2 {
  font-weight: 500;
  font-size: 20px;
  line-height: 140%;
}
@mixin font-text3 {
  font-weight: 700;
  font-size: 14px;
  line-height: 122%;
  letter-spacing: 1px;
  text-transform: uppercase;
}
@mixin font-text4 {
  font-weight: 500;
  font-size: 14px;
  line-height: 122%;
  letter-spacing: 2px;
  text-transform: uppercase;
}
@mixin font-text5 {
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
}
@mixin font-text6 {
  font-weight: 600;
  font-size: 15px;
  line-height: 140%;
}
@mixin font-text7 {
  font-weight: 500;
  font-size: 15px;
  line-height: 140%;
}
@mixin font-text8 {
  font-weight: 500;
  font-size: 12px;
  line-height: 140%;
  letter-spacing: 1px;
  text-transform: uppercase;
}
@mixin font-text9 {
  font-weight: 600;
  font-size: 13px;
  line-height: 152%;
}
@mixin font-text10 {
  font-weight: 500;
  font-size: 13px;
  line-height: 140%;
}
@mixin font-h1-landing {
  font-weight: 700;
  font-size: 16px;
  line-height: 140%;
  letter-spacing: 2px;
  text-transform: uppercase;
}
@mixin font-h2-landing {
  font-weight: 700;
  font-size: 28px;
  line-height: 122%;
  letter-spacing: 2px;
  text-transform: uppercase;
}
@mixin font-nexa-landing {
  font-family: 'NexaLight';
  font-weight: 300;
  font-size: 15px;
  line-height: 185%;
  letter-spacing: 0.015em;
}
@mixin font-button-landing {
  font-weight: 700;
  font-size: 14px;
  line-height: 122%;
  letter-spacing: 2px;
  text-transform: uppercase;
}
@mixin font-menu-title {
  font-weight: 700;
  font-size: 14px;
  line-height: 122%;
  letter-spacing: 2px;
  text-transform: uppercase;
}
@mixin font-button-title {
  font-weight: 700;
  font-size: 14px;
  line-height: 122%;
  letter-spacing: 1px;
  text-transform: uppercase;
}
@mixin font-longread-title {
  font-weight: 400;
  font-size: 16px;
  line-height: 170%;
}