.databox{
  &.st1{
    li{
      position: relative;
      display: flex;
      align-items: flex-start;
      margin: 0 0 16px!important;
      &:before{
        position: absolute;
        top: 10px;
        left: 0;
        display: block;
        width: 100%;
        height: 0;
        border-top: 2px dotted var(--color-gray3);
        content: "";
      }
      &:last-child{
        margin: 0!important;
      }
      .label{
        position: relative;
        margin: 0 24px 0 0;
        @include font-text8;
        span{
          position: relative;
          display: inline-block;
          padding: 0 10px 0 0;
          color: var(--color-gray2);
        }
      }
      .desc{
        flex: 1 1 auto;
        position: relative;
        margin: -3px 0 0;
        padding: 0 0 0 20px;
        @include font-text7;
        color: var(--color-black);
        a,
        button{
          @include font-text7;
          color: var(--color-black);
          text-decoration: underline;
          &:hover{
            text-decoration: none;
          }
        }
      }
    }
  }
  &.st2{
    li{
      text-align: right;
      white-space: nowrap;
      margin: 0 0 19px;
      &:last-child{
        margin: 0;
      }
      .label{
        @include font-text8;
      }
      .desc{
        @include font-text7;
      }
    }
  }
  &.st3{
    li{
      padding: 0 0 10px;
      font-size: 12px;
      line-height: 140%;
      font-weight: 500;
      letter-spacing: 1px;
      text-transform: uppercase;
      color: var(--color-gray1);
      &.desc{
        padding: 0 0 16px;
        font-size: 15px;
        font-weight: 500;
        line-height: 140%;
        color: var(--color-black);
        text-transform: none;
      }
      &:last-child{
        padding: 0;
      }
    }
  }
  &.st4{
    display: flex;
    li{
      a{
        position: relative;
        display: flex;
        align-items: center;
        padding: 0 18px;
        img{
          display: block;
          max-width: 23px;
          min-width: 23px;
          height: 28px;
          margin: 0 20px 0 0;
        }
        span{
          display: block;
          font-size: 15px;
          line-height: 140%;
          color: var(--color-gray2);
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          &.pl{
            position: absolute;
            bottom: calc(100% + 15px);
            left: 0;
            width: auto;
            padding: 13px 18px;
            background: var(--color-white);
            font-size: 15px;
            line-height: 140%;
            color: var(--color-gray2);
            box-shadow: 5px 5px 20px rgba(14, 77, 91, 0.1);
            opacity: 0;
            visibility: hidden;
            transition: all .2s ease;
          }
        }
        &:hover{
          span.pl{
            opacity: 1;
            visibility: visible;
          }
        }
        svg{
          display: block;
          max-width: 14px;
          min-width: 14px;
          height: 12px;
          margin: 0 0 0 10px;
          fill: var(--color-gray2);
        }
      }
    }
  }
  &.st5{
    li{
      display: grid;
      margin: 0 0 5px;
      &:first-child{
        .item{
          color: var(--color-gray2);
        }
      }
      &:last-child{
        margin: 0
      }
      .item{
        padding: 16px 22px;
        @include font-text10;
        color: var(--color-black);
        white-space: nowrap;
        svg{
          display: block;
          width: 16px;
          height: 12px;
          fill: var(--color-green-plus);
        }
        &.inactive{
          svg{
            width: 14px;
            height: 14px;
            fill: var(--color-red);
          }
        }
      }
    }
  }
  &.st6{
    li{
      display: flex;
      justify-content: space-between;
      padding: 22px 25px;
      margin: 0 0 14px;
      background: var(--color-blue_10);
      &:last-child{
        margin: 0;
      }
      .side{
        flex: 1 1 auto;
        color: var(--color-blue);
        .name{
          @include font-text6;
        }
        .desc{
          @include font-text5;
        }
      }
      .info{
        display: flex;
        align-items: flex-start;
        padding: 0 0 0 40px;
        .status{
          display: flex;
          align-items: center;
          svg{
            display: block;
            max-width: 14px;
            min-width: 14px;
            height: 11px;
            fill: var(--color-blue-plus);
          }
          span{
            display: block;
            padding: 0 0 0 10px;
            @include font-text6;
            color: var(--color-blue-plus);
          }
        }
        .actions{
          margin: 4px -12px 0 38px;
          button{
            width: 15px;
            height: 15px;
            svg{
              display: block;
              width: 3px;
              height: 100%;
              fill: var(--color-blue);
            }
          }
        }
      }
    }
  }
  &.st7{
    li{
      display: flex;
      padding: 0 0 17px;
      &.title{
        padding: 0 0 24px;
        @include font-text6;
        color: var(--color-black);
      }
      &:last-child{
        padding: 0;
      }
      .label{
        @include font-text8;
        text-transform: uppercase;
        color: var(--color-gray2)
      }
      .desc{
        margin: -4px 0 0;
        @include font-text7;
        color: var(--color-black);
      }
    }
  }
  &.st8{
    li{
      position: relative;
      display: flex;
      align-items: center;
      padding: 0 0 16px 18px;
      &:last-child{
        padding: 0 0 0 18px;
      }
      &:before,
      &:after{
        position: absolute;
        display: block;
        content: "";
      }
      &:before{
        top: 0;
        left: 4px;
        width: 1px;
        height: 100%;
        background: var(--color-gray3);
      }
      &:after{
        top: 5px;
        left: 0;
        width: 7px;
        height: 7px;
        border: 1px solid var(--color-gray3);
        border-radius: 50%;
        background: var(--color-white);
      }
      span{
        display: block;
        @include font-text10;
        &:first-child{
          color: var(--color-black);
        }
        &:last-child{
          padding: 0 0 0 30px;
          color: var(--color-gray2);
        }
      }
    }
  }
}