footer.main{
  margin: -414px 0 0;
  padding: 70px 0 63px;
  background: var(--color-footer);
  .wrap{
    .top{
      display: flex;
      padding: 0 0 70px;
      a.logo{
        font-weight: 500;
        font-size: 32px;
        line-height: 122%;
        letter-spacing: 2px;
        text-transform: uppercase;
        color: var(--color-white);
      }
      .nav{
        display: flex;
        margin: 0 0 0 auto;
        ul{
          padding: 0 72px 0 0;
          &:first-child{
            li{
              padding: 0 0 3px;
            }
          }
          &:last-child{
            padding: 0;
            li{
              padding: 0 0 13px;
            }
          }
          li{
            display: flex;
            @include font-nexa-landing;
            color: var(--color-gray2);
            &:first-child{
              padding: 0 0 19px!important;
              @include font-button-landing;
              color: var(--color-white);
            }
            &:last-child{
              padding: 0!important;
            }
            svg{
              display: block;
              fill: var(--color-gray2);
              &.email{
                max-width: 20px;
                min-width: 20px;
                height: 13px;
                margin: 8px 12px 0 0;
              }
              &.location{
                max-width: 15px;
                min-width: 15px;
                height: 21px;
                margin: 4px 14px 0 0;
              }
              &.phone{
                max-width: 20px;
                min-width: 20px;
                height: 20px;
                margin: 4px 12px 0 0;
              }
            }
            a{
              display: block;
              @include font-nexa-landing;
              color: var(--color-gray2);
              transition: color .2s ease;
              &:hover{
                color: var(--color-white);
              }
            }
          }
        }
      }
    }
    .social{
      position: relative;
      &:before{
        position: absolute;
        top: 50%;
        left: 0;
        display: block;
        width: 100%;
        height: 1px;
        background: var(--color-gray2);
        content: "";
      }
      ul{
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 230px;
        margin: 0 auto;
        background: var(--color-footer);
        li{
          padding: 0 4px;
          a{
            display: flex;
            justify-content: center;
            align-items: center;
            width: 55px;
            height: 55px;
            border: 1px solid var(--color-gray2);
            transition: border-color .2s ease;
            svg{
              display: block;
              fill: var(--color-gray3);
              transition: fill .2s ease;
              &.facebook{
                width: 8px;
                height: 15px;
              }
              &.google{
                width: 24px;
                height: 15px;
              }
              &.twitter{
                width: 18px;
                height: 15px;
              }
            }
            &:hover{
              border-color: var(--color-white);
              svg{
                fill: var(--color-white);
              }
            }
          }
        }
      }
    }
  }
}