section.organization{
  position: relative;
  .nav{
    position: relative;
    margin: 48px 16px 0;
    padding: 0 20px;
    &:before,
    &:after{
      position: absolute;
      top: 0;
      display: block;
      width: 30px;
      height: 20px;
      z-index: 2;
      content: "";
    }
    &:before{
      left: 0;
      background: linear-gradient(270deg, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
    }
    &:after{
      right: 0;
      background: linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
    }
    ul{
      position: relative;
      z-index: 1;
      li{
        width: auto;
        margin-bottom: -3px;
        padding-bottom: 3px;
        a{
          position: relative;
          display: block;
          padding: 0 0 30px;
          border-bottom: 3px solid var(--color-transparent);
          @include font-menu-title;
          color: var(--color-gray3);
          transition: all .35s ease;
          z-index: 1;
          &:hover,
          &.active{
            color: var(--color-black);
          }
          &.active{
            border-color: var(--color-black);
          }
        }
      }
    }
  }
  .swiper-button{
    position: absolute;
    top: 0;
    z-index: 3;
    cursor: pointer;
    transition: opacity .35s ease;
    svg{
      display: block;
      width: 18px;
      height: 18px;
      fill: none;
      stroke: var(--color-gray2);
      stroke-width: 2;
    }
    &.prev{
      left: 20px;
      transform: rotate(-180deg);
    }
    &.next{
      right: 20px;
    }
    &.swiper-button-disabled{
      opacity: .2;
      cursor: default;
    }
  }
  .pagetitle{
    padding: 80px 0 0;
    font-weight: 500;
    font-size: 36px;
    line-height: 110%;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: var(--color-black);
    &.p2{
      padding: 26px 0 0;
    }
  }
  .btns{
    display: flex;
    align-items: center;
    padding: 50px 0 0;
    &.m2{
      margin: 0 0 50px;
    }
    button{
      justify-content: flex-start;
      width: 312px;
      height: 59px;
      margin: 0 16px 0 0;
      padding: 0 23px;
      background: var(--color-gray4);
      transition: background .35s ease;
      &:last-child{
        margin: 0;
      }
      svg{
        display: block;
        fill: var(--color-button);
        transition: fill .35s ease;
        &.plus{
          max-width: 24px;
          min-width: 24px;
          height: 24px;
          margin: 0 17px 0 0;
        }
        &.file{
          max-width: 17px;
          min-width: 17px;
          height: 24px;
          margin: 0 21px 0 0;
        }
        &.pencil{
          max-width: 24px;
          min-width: 24px;
          height: 24px;
          margin: 0 17px 0 0;
        }
        &.arrow{
          max-width: 24px;
          min-width: 24px;
          height: 24px;
          margin: 0 17px 0 0;
        }
        &.close{
          max-width: 24px;
          min-width: 24px;
          height: 24px;
          margin: 0 17px 0 0;
          fill: var(--color-red);
        }
      }
      span{
        display: block;
        @include font-text7;
        color: var(--color-button);
        transition: color .35s ease;
        &.red{
          color: var(--color-red);
        }
      }
      &:hover{
        background: var(--color-button);
        svg{
          fill: var(--color-white);
        }
        span{
          color: var(--color-white);
        }
      }
    }
  }
  form.search{
    position: relative;
    display: flex;
    height: 59px;
    margin: 50px 0 0;
    svg{
      position: absolute;
      top: 50%;
      left: 20px;
      display: block;
      width: 22px;
      height: 22px;
      margin: -11px 0 0;
      fill: var(--color-gray2);
      content: "";
    }
    input{
      flex: 1 1 auto;
      height: 100%;
      padding: 0 20px 0 62px;
      background: var(--color-gray4);
    }
    button{
      max-width: 245px;
      min-width: 245px;
      height: 100%;
    }
  }
  .info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 32px 0 0;
    .result{
      @include font-text7;
      color: var(--color-gray2);
      span{
        color: var(--color-black);
      }
    }
    .sort{
      position: relative;
      button.toggle{
        span{
          display: block;
          padding: 0 12px 0 0;
          @include font-text7;
          color: var(--color-black);
        }
        svg{
          display: block;
          max-width: 18px;
          min-width: 18px;
          height: 18px;
          fill: none;
          stroke: var(--color-black);
          stroke-width: 2;
          transform: rotate(0deg);
          transition: all .2s ease;
        }
        &.active{
          svg{
            transform: rotate(-180deg);
          }
        }
      }
      ul{
        position: absolute;
        top: 100%;
        right: 0;
        display: none;
        min-width: 100%;
        margin: 20px 0 0;
        box-shadow: 4px 4px 10px rgba(0,0,0,.1);
        &:before,
        &:after{
          position: absolute;
          display: block;
          z-index: 1;
          content: "";
        }
        &:before{
          top: -6px;
          left: 20px;
          width: 20px;
          height: 20px;
          background: var(--color-white);
          transform: rotate(-45deg);
          box-shadow: 0 0 9px rgba(0, 0, 0, .1);
        }
        &:after{
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: var(--color-white);
        }
        li{
          position: relative;
          z-index: 2;
          button{
            justify-content: flex-start;
            width: 100%;
            padding: 4px 10px;
            background: var(--color-white);
            @include font-text7;
            color: var(--color-black);
            white-space: nowrap;
            transition: background .2s ease;
            &:hover{
              background: var(--color-gray5);
            }
          }
        }
      }
    }
  }
  ul.result{
    padding: 32px 0 0;
    li{
      display: flex;
      padding: 22px 0;
      background: var(--color-gray4);
      margin: 0 0 15px;
      @include font-text10;
      &:first-child{
        .item{
          color: var(--color-gray2);
        }
      }
      &:last-child{
        margin: 0;
      }
      .item{
        position: relative;
        padding: 0 0 0 20px;
        &:nth-child(1){
          max-width: 44px;
          min-width: 44px;
        }
        &:nth-child(2){
          max-width: 128px;
          min-width: 128px;
        }
        &:nth-child(3){
          flex: 1 1 auto;
        }
        &:nth-child(4){
          max-width: 180px;
          min-width: 180px;
        }
        &:nth-child(5){
          max-width: 150px;
          min-width: 150px;
        }
        &:nth-child(6){
          max-width: 120px;
          min-width: 120px;
        }
        &:nth-child(7){
          max-width: 160px;
          min-width: 160px;
        }
        &:nth-child(8){
          max-width: 140px;
          min-width: 140px;
        }
        &:nth-child(9){
          max-width: 52px;
          min-width: 52px;
          padding: 0 10px 0 20px;
        }
        &.green{
          color: var(--color-green-plus);
        }
        &.blue{
          color: var(--color-blue);
        }
        button{
          display: flex;
          justify-content: center;
          align-items: center;
          width: 22px;
          height: 22px;
          svg{
            display: block;
            max-width: 3px;
            min-width: 3px;
            height: 15px;
          }
        }
        .hidden{
          position: absolute;
          top: 26px;
          right: 24px;
          display: none;
          width: 294px;
          padding: 21px 24px 24px;
          background: var(--color-white);
          box-shadow: 5px 5px 20px 4px rgba(14, 77, 91, 0.1);
          z-index: 1;
          &.open{
            display: block;
          }
          button{
            display: block;
            margin: 0 0 10px;
            @include font-text5;
            color: var(--color-black);
            text-align: left;
            white-space: nowrap;
            &:hover{
              color: var(--color-red);
            }
            &:last-child{
              margin: 0;
              color: var(--color-red);
              &:hover{
                color: var(--color-black);
              }
            }
          }
        }
      }
    }
  }
  a.to_back{
    margin: 32px 0 0;
  }
  .graybox{
    margin: 0 0 35px;
    padding: 40px 30px 46px;
    background: var(--color-gray4_40);
    .subtitle{
      padding: 0 0 35px;
    }
    ol.st1{
      li{
        .label{
          max-width: 410px;
          min-width: 410px;
          span{
            background: var(--color-gray10);
          }
        }
        .desc{
          background: var(--color-gray10);
        }
      }
    }
    ol.st4{
      li{
        max-width: 312px;
        margin: 0 20px 0 0;
        &:last-child{
          margin: 0;
        }
        a{
          padding: 16px 18px;
          background: var(--color-gray4);
        }
      }
    }
    ol.st5{
      li{
        margin: 0 0 15px;
        background: var(--color-gray4);
        grid-template-columns: 60px auto 160px 160px 180px 160px 160px 60px;
        &:last-child{
          margin: 0;
        }
        .item{
          position: relative;
          button{
            display: flex;
            justify-content: center;
            align-items: center;
            width: 22px;
            height: 22px;
            svg{
              display: block;
              max-width: 3px;
              min-width: 3px;
              height: 15px;
              fill: var(--color-gray2);
            }
          }
          .hidden{
            position: absolute;
            top: 18px;
            right: 40px;
            display: none;
            width: 294px;
            padding: 21px 24px 24px;
            background: var(--color-white);
            box-shadow: 5px 5px 20px 4px rgba(14, 77, 91, 0.1);
            z-index: 1;
            &.open{
              display: block;
            }
            button{
              display: block;
              @include font-text5;
              color: var(--color-black);
              text-align: left;
              white-space: nowrap;
              &:hover{
                color: var(--color-red);
              }
            }
            .hidden{
              position: absolute;
              top: 26px;
              right: 24px;
              display: none;
              width: 294px;
              padding: 21px 24px 24px;
              background: var(--color-white);
              box-shadow: 5px 5px 20px 4px rgba(14, 77, 91, 0.1);
              z-index: 1;
              &.open{
                display: block;
              }
              button{
                display: block;
                margin: 0 0 10px;
                @include font-text5;
                color: var(--color-black);
                text-align: left;
                white-space: nowrap;
                &:hover{
                  color: var(--color-red);
                }
                &:last-child{
                  margin: 0;
                  color: var(--color-red);
                  &:hover{
                    color: var(--color-black);
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}