* {
  outline: 0;
  box-sizing: border-box;
}
html {
  min-width: 300px;
}
html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  background: var(--color-white);
  font-family: 'Inter', sans-serif;
}
ul,ol{
  padding: 0;
  margin: 0;
  list-style: none;
  li{
    padding: 0;
    margin: 0;
  }
}
input, textarea{
  display: block;
  border: 0;
  background: var(--color-transparent);
}
a{
  text-decoration: none;
}
button{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
  border: 0;
  background: transparent;
  transition: all .2s ease;
  cursor: pointer;
}
h1,h2,h3,h4,h5,h6{
  padding: 0;
  margin: 0;
  font-weight: 400;
}
.app{
  width: 100%;
  height: auto;
  main.content{
    min-height: calc(100Vh - 90px);
    padding: 0 0 414px;
  }
}
.wrap{
  max-width: 1248px;
  min-width: 300px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 0 20px;
}
.btn{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 315px;
  height: 70px;
  background: var(--color-button);
  @include font-button-landing;
  color: var(--color-white);
  transition: background .2s ease;
  &:hover{
    background: var(--color-button-hover);
  }
}
.morebtn{
  display: flex;
  align-items: center;
  span{
    display: block;
    @include font-button-landing;
    color: var(--color-black);
  }
  svg{
    position: relative;
    left: 0;
    display: block;
    max-width: 54px;
    min-width: 54px;
    height: 14px;
    margin: 0 0 0 14px;
    transition: left .2s ease;
  }
  &:hover{
    svg{
      left: 10px;
    }
  }
}
.roundbtn{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 45px;
  border-radius: 23px;
  transition: all .2s ease;
  span{
    display: block;
    @include font-text3;
  }
  &.st1{
    background: var(--color-blue_10);
    color: var(--color-blue);
    &:hover{
      background: var(--color-blue);
      color: var(--color-white);
    }
  }
  &.st2{
    background: var(--color-green_10);
    color: var(--color-green-plus);
    &:hover{
      background: var(--color-green-plus);
      color: var(--color-white);
    }
  }
}
.plusbtn{
  justify-content: flex-start;
  width: 312px;
  height: 59px;
  padding: 0 23px;
  background: var(--color-gray3_20);
  transition: background .2s ease;
  svg{
    display: block;
    max-width: 24px;
    min-width: 24px;
    height: 24px;
    fill: var(--color-black);
    transition: fill .2s ease;
  }
  span{
    display: block;
    padding: 0 0 0 17px;
    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
    color: var(--color-button);
    transition: color .2s ease;
  }
  &:hover{
    background: var(--color-button-hover);
    svg{
      fill: var(--color-white);
    }
    span{
      color: var(--color-white);
    }
  }
}
section.pageinfo{
  position: relative;
  width: 100%;
  .bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    img{
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .info{
    position: relative;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    align-content: center;
    padding-top: 84px;
    padding-bottom: 84px;
    z-index: 2;
    h1{
      width: 100%;
      font-weight: 500;
      font-size: 50px;
      line-height: 140%;
      letter-spacing: 2px;
      text-transform: uppercase;
      color: var(--color-black);
    }
    .desc{
      width: 100%;
      padding: 4px 0 0;
      @include font-h1-landing;
      color: var(--color-black);
    }
    &.white{
      h1,
      .desc{
        color: var(--color-white);
      }
    }
  }
  &.search{
    padding: 118px 0;
    transition: padding .2s ease;
    transition-delay: .1s;
    &:before{
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0.45));
      visibility: hidden;
      opacity: 0;
      transition: all .2s ease;
      z-index: 2;
      content: "";
    }
    .bg{
      overflow: hidden;
      img{
        display: block;
        width: 100%;
        min-height: 100%;
      }
    }
    .filters{
      position: relative;
      z-index: 3;
      .desc{
        padding: 0 0 11px;
        @include font-h5;
        color: var(--color-additional-text);
        transition: color .2s ease;
      }
      h1{
        padding: 0 0 40px;
        @include font-h1;
        color: var(--color-button);
        transition: color .2s ease;
      }
      form{
        position: relative;
        display: block;
        width: 100%;
        .main{
          position: relative;
          display: flex;
          justify-content: space-between;
          align-items: center;
          height: 59px;
          margin: 0 0 40px;
          svg.search{
            position: absolute;
            top: 50%;
            left: 20px;
            display: block;
            width: 22px;
            height: 22px;
            margin: -11px 0 0;
            fill: var(--color-gray2);
          }
          input{
            flex: 1 1 auto;
            padding: 0 172px 0 62px;
          }
          button{
            &[type="button"]{
              position: absolute;
              top: 0;
              right: 245px;
              justify-content: space-between;
              width: 96px;
              height: 100%;
              svg{
                display: block;
                max-width: 25px;
                min-width: 25px;
                height: 14px;
                margin: 0 14px 0 0;
                fill: var(--color-filter);
              }
              span{
                display: block;
                @include font-text7;
                color: var(--color-filter);
              }
            }
            &[type="submit"]{
              width: 259px;
              height: 100%;
            }
          }
        }
        .hidden{
          display: none;
          .flexbox{
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            padding: 0 0 25px;
            .fieldset{
              width: calc(33.333% - 9px);
              .label{
                color: var(--color-white);
              }
              .select2{
                span.selection{
                  .select2-selection{
                    background: var(--color-white);
                  }
                }
              }
              .items2{
                display: flex;
                justify-content: space-between;
                align-items: center;
                .datepicker{
                  width: calc(50% - 2px)!important;
                }
              }
              .items3{
                display: flex;
                justify-content: space-between;
                align-items: center;
                .select2,
                input{
                  width: calc(33.333% - 2px)!important;
                }
              }
              &.checkboxes{
                padding: 26px 0 0;
                .checkbox{
                  &:first-child{
                    margin: 0 0 16px;
                  }
                  label{
                    padding: 0;
                    color: var(--color-white);
                  }
                }
              }
            }
            button.clear{
              span{
                display: block;
                padding: 0 8px 0 0;
                @include font-text6;
                color: var(--color-white);
                white-space: nowrap;
              }
              svg{
                display: block;
                max-width: 20px;
                min-width: 20px;
                height: 20px;
                stroke: var(--color-white);
              }
            }
          }
        }
      }
    }
    &.open{
      padding: 118px 0 20px;
      &:before{
        visibility: visible;
        opacity: 1;
      }
      .filters{
        .desc{
          color: var(--color-white);
        }
        h1{
          color: var(--color-white);
        }
      }
    }
  }
}
.form{
  .label,
  label{
    display: block;
    padding: 0 0 6px;
    @include font-text6;
  }
  fieldset{
    padding: 0 0 35px;
    margin: 0;
    border: 0;
  }
  input{
    display: block;
    width: 100%;
    height: 59px;
    padding: 0 20px;
    border: 0;
    background: var(--color-white);
    @include font-text5;
    color: var(--color-black);
    &::placeholder{
      color: var(--color-gray3);
    }
    &.error{
      background: var(--color-red)!important;
      color: var(--color-white);
      &::placeholder{
        color: var(--color-white);
      }
    }
  }
  select.customselect{
    width: 0;
    height: 0;
    visibility: hidden;
    opacity: 0;
    &.error{
      & + .select2{
        background: var(--color-red);
        span.selection{
          .select2-selection{
            background: var(--color-red);
            .select2-selection__placeholder{
              color: var(--color-white);
            }
            .select2-selection__arrow{
              b{
                border-color: var(--color-white);
              }
            }
          }
        }
      }
    }
  }
  .checkbox{
    input{
      display: none;
    }
    label{
      position: relative;
      display: flex;
      align-items: center;
      @include font-text5;
      cursor: pointer;
      &:before,
      &:after{
        display: block;
        content: "";
      }
      &:before{
        max-width: 20px;
        min-width: 20px;
        height: 20px;
        margin: 0 14px 0 0;
        background: var(--color-white);
      }
      &:after{
        position: absolute;
        top: 50%;
        left: 4px;
        width: 13px;
        height: 10px;
        margin: -5px 0 0;
        background: url(../img/checkbox.svg) 0 0/100% 100% no-repeat;
        visibility: hidden;
        opacity: 0;
        transition: all .2s ease;
      }
    }
    input:checked + label{
      &:after{
        visibility: visible;
        opacity: 1;
      }
    }
  }
  .select2{
    display: block;
    width: 100%!important;
    height: 59px;
    background: var(--color-gray4);
    span{
      display: block;
      &.selection{
        width: 100%;
        height: 100%;
        .select2-selection{
          display: flex;
          justify-content: space-between;
          align-items: center;
          width: 100%;
          height: 100%;
          padding: 0 22px 0 20px;
          border: 0;
          border-radius: 0;
          background: var(--color-gray4);
          .select2-selection__rendered{
            padding: 0;
            @include font-text5;
            color: var(--color-black);
            .select2-selection__placeholder{
              color: var(--color-gray3);
            }
          }
          .select2-selection__arrow{
            top: 0;
            right: 0;
            width: 59px;
            height: 100%;
            b{
              position: absolute;
              top: 50%;
              left: 50%;
              display: block;
              width: 12px;
              height: 12px;
              margin: -10px 0 0 -6px;
              border: 0 solid var(--color-gray2);
              border-width: 2px 2px 0 0;
              transform: rotate(135deg);
              transition: all .2s ease;
            }
          }
        }
      }
    }
    &.select2-container--open{
      span{
        &.selection{
          .select2-selection{
            .select2-selection__arrow{
              b{
                margin-top: -2px;
                transform: rotate(-45deg);
              }
            }
          }
        }
      }
    }
  }
  select.withico + .select2{
    position: relative;
    padding: 0 0 0 40px;
    &:before{
      position: absolute;
      top: 18px;
      left: 20px;
      display: block;
      width: 22px;
      height: 22px;
      background: url(../img/search.svg) 0 0 no-repeat;
      background-size: 100% 100%;
      content: "";
    }
  }
  .datepicker{
    position: relative;
    width: 100%;
    height: 59px;
    &:after{
      position: absolute;
      top: 22px;
      right: 18px;
      display: block;
      width: 16px;
      height: 17px;
      background: url(../img/calendar.svg) 0 0/100% 100% no-repeat;
      content: "";
    }
    input{
      width: 100%;
      height: 59px;
    }
  }
}
body > .select2-container{
  display: block;
  span{
    display: block;
    &.select2-dropdown{
      border: 1px solid var(--color-gray3);
      border-radius: 0;
      background: var(--color-white);
      .select2-search{
        input{
          border-color: var(--color-gray3);
        }
      }
      .select2-results{
        ul{
          li{
            transition: all .2s ease;
            &:hover{
              background: var(--color-blue-plus);
              color: var(--color-white);
            }
            &.select2-results__option--selected{
              background: var(--color-blue);
              color: var(--color-white);
            }
          }
        }
      }
    }
  }
}
.pagination{
  position: relative;
  width: 100%;
  height: 59px;
  margin: 45px 0 163px;
  ul{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    li{
      padding: 0 22px;
      &:first-child{
        padding: 0 34px 0 0;
      }
      &:last-child{
        padding-right: 0 0 0 34px;
      }
      a,
      span{
        display: block;
        @include font-text7;
        color: var(--color-black);
      }
      a{
        transition: color .2s ease;
        &:hover{
          color: var(--color-blue-plus);
        }
        svg{
          display: block;
          width: 34px;
          height: 9px;
          &.prev{
            transform: rotate(-180deg);
          }
        }
      }
      span{
        &.current{
          color: var(--color-blue-plus);
        }
      }
    }
  }
  .more{
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    button{
      width: 245px;
      height: 59px;
      border: 1px solid var(--color-black);
      @include font-button-title;
      color: var(--color-black);
      transition: all .2s ease;
      &:hover{
        background: var(--color-black);
        color: var(--color-white);
      }
    }
  }
}
a.to_back{
  display: flex;
  align-items: center;
  svg{
    display: block;
    width: 10px;
    height: 12px;
    fill: none;
    stroke: var(--color-black);
    transition: stroke .2s ease;
  }
  span{
    display: block;
    padding: 0 0 0 8px;
    @include font-text7;
    color: var(--color-black);
    transition: color .2s ease;
  }
  &:hover{
    svg{
      stroke: var(--color-button-hover);
    }
    span{
      color: var(--color-button-hover);
    }
  }
}
.pagetitle{
  display: flex;
  align-items: center;
  strong{
    display: block;
    @include font-h2;
    color: var(--color-black);
  }
  span.pl{
    position: relative;
    display: block;
    max-width: 13px;
    min-width: 13px;
    height: 13px;
    margin: 0 0 0 20px;
    border-radius: 50%;
    background: var(--color-green-plus);
    cursor: pointer;
    span{
      position: absolute;
      bottom: calc(100% + 10px);
      left: 0;
      display: block;
      padding: 13px 18px;
      border-radius: 0 5px 5px 0;
      background: var(--color-white);
      font-size: 13px;
      font-weight: 500;
      line-height: 140%;
      color: var(--color-green-plus);
      white-space: nowrap;
      box-shadow: 5px 5px 20px rgba(14, 77, 91, 0.1);
      opacity: 0;
      visibility: hidden;
      transition: all .2s ease;
    }
    &:hover{
      span{
        opacity: 1;
        visibility: visible;
      }
    }
  }
}
.subtitle{
  @include font-h3;
}
.tabbox{
  ul{
    position: relative;
    &.nav{
      display: inline-flex;
      align-items: center;
      border-bottom: 3px solid var(--color-white);
      li{
        margin: 0 0 -3px;
      }
    }
    &.tabs{
      & > li{
        display: none;
        width: 100%;
        &.active{
          display: block;
        }
      }
      
    }
  }
}
.loader{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--color-black_50);
  z-index: 10000;
  .info{
    width: 584px;
    padding: 66px 0 54px;
    background: var(--color-white);
    .desc{
      padding: 0 0 42px;
      @include font-h5;
      letter-spacing: 1px;
      text-transform: uppercase;
      text-align: center;
    }
    .circle{
      width: 70px;
      height: 70px;
      margin: 0 auto;
      img{
        display: block;
        width: 100%;
        height: 100%;
        animation: loader 1.5s linear infinite;
      }
    }
  }
}
@keyframes loader {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.errorbox{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--color-black_50);
  z-index: 10000;
  .info{
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    width: 950px;
    height: 100Vh;
    padding: 0 140px;
    background: var(--color-white);
    .title{
      padding: 0 0 28px;
      @include font-h2;
      letter-spacing: 1.8px;
    }
    .desc{
      padding: 0 0 68px;
      @include font-longread-title;
    }
    .btns{
      display: flex;
      align-items: center;
      button{
        max-width: 318px;
        min-width: 318px;
        svg{
          display: block;
          max-width: 23px;
          min-width: 23px;
          height: 21px;
          fill: var(--color-white);
        }
        span{
          display: block;
          padding: 0 0 0 14px;
          letter-spacing: 1px;
        }
      }
      a{
        max-width: 245px;
        min-width: 245px;
        margin: 0 0 0 20px;
      }
    }
  }
}
.errorpage{
  .graybox{
    padding: 61px 0 63px;
    background: var(--color-gray4);
    &:before{
      content: "";
    }
    .ico{
      width: 84px;
      height: 84px;
      margin: 0 auto;
      svg{
        display: block;
        width: 100%;
        height: 100%;
        fill: var(--color-button);
      }
    }
    .desc{
      padding: 43px 0 0;
      @include font-h2;
      text-align: center;
    }
  }
  a{
    margin: 43px auto 0;
  }
}













