section.purchase{
  padding: 33px 0 0;
  .pagetitle{
    padding: 26px 0 0;
  }
  .maininfo{
    display: flex;
    justify-content: space-between;
    padding: 35px 0 82px;
    .side{
      flex: 1 1 auto;
      padding: 0 140px 0 0;
      .desc{
        @include font-longread-title;
        color: var(--color-black);
      }
      ul.btnsbox{
        display: flex;
        align-items: center;
        padding: 42px 0 0;
        li{
          padding: 0 20px 0 0;
          &:last-child{
            padding: 0;
          }
          button{
            &.st1{
              padding: 0 40px;
            }
            &.st2{
              padding: 0 30px;
              &:hover{
                svg{
                  fill: var(--color-white);
                }
              }
            }
            svg{
              display: block;
              max-width: 15px;
              min-width: 15px;
              height: 15px;
              margin: 0 8px 0 0;
              fill: var(--color-green-plus);
            }
          }
        }
      }
      ol.databox{
        padding: 54px 0 0;
        li{
          .label{
            max-width: 354px;
            min-width: 354px;
            span{
              background: var(--color-white);
            }
          }
          .desc{
            background: var(--color-white);
            @include font-text7;
          }
        }
      }
    }
    .price{
      max-width: 360px;
      min-width: 360px;
      margin: -70px 0 0;
      padding: 44px 30px 40px;
      background: var(--color-gray4);
      .label{
        @include font-text7;
        &:nth-child(3){
          padding: 30px 0 0;
        }
      }
      .num{
        padding: 30px 0 0;
        font-size: 32px;
        font-weight: 500;
        line-height: 140%;
        span{
          @include font-text2;
          color: var(--color-gray2);
        }
      }
      .date{
        padding: 8px 0 0;
        @include font-text6;
        color: var(--color-green-plus);
      }
      button{
        width: 100%;
        margin: 70px 0 0;
      }
    }
  }
  .graybox{
    margin: 0 0 40px;
    padding: 40px 30px 46px;
    background: var(--color-gray4_40);
    .subtitle{
      padding: 0 0 35px;
    }
  }
  .about{
    ol.databox{
      li{
        .label{
          max-width: 470px;
          min-width: 470px;
          span{
            background: var(--color-gray10);
          }
        }
        .desc{
          background: var(--color-gray10);
        }
      }
    }
  }
  .documents{
    ol.databox{
      margin: 0 0 -20px;
      li{
        margin: 0 20px 20px 0;
        a{
          width: 312px;
          height: 59px;
          background: var(--color-gray4);
          transition: background .2s ease;
          &:hover{
            background: var(--color-gray3_20);
          }
        }
      }
    }
  }
  .lots{
    margin: 0 0 40px;
    padding: 53px 30px 50px;
    background: var(--color-additional-text2);
    ul{
      li{
        .flexbox{
          display: flex;
          justify-content: space-between;
          .side{
            flex: 1 1 auto;
            .top{
              display: flex;
              align-items: center;
              strong{
                display: block;
                padding: 0 34px 0 0;
              }
              span{
                display: flex;
                align-items: center;
                svg{  
                  display: block;
                  max-width: 12px;
                  min-width: 12px;
                  height: 12px;
                  margin: 0 11px 0 0;
                  fill: var(--color-black);
                }
                span{
                  display: block;
                  @include font-text5;
                  color: var(--color-black);
                }
              }
            }
            .desc{
              padding: 25px 0 0;
              @include font-text5;
              color: var(--color-black);
            }
            .infobox{
              display: flex;
              align-items: center;
              .item{
                position: relative;
                display: flex;
                align-items: center;
                padding: 10px 0 0;
                &:first-child{
                  padding-right: 35px;
                } 
                &:last-child{
                  padding-right: 0;
                }
                svg{
                  display: block;
                  fill: var(--color-black_70);
                  &.time{
                    max-width: 13px;
                    min-width: 13px;
                    height: 13px;
                    cursor: pointer;
                  }
                  &.location{
                    max-width: 9px;
                    min-width: 9px;
                    height: 13px;
                  }
                  &:hover + .tooltip{
                    opacity: 1;
                    visibility: visible;
                  }
                }
                span{
                  display: block;
                  padding: 0 0 0 10px;
                  @include font-text5;
                  color: var(--color-black);
                }
                .tooltip{
                  position: absolute;
                  bottom: 28px;
                  left: 0;
                  padding: 13px 18px 10px;
                  border-radius: 10px 10px 10px 0;
                  background: var(--color-white);
                  box-shadow: 5px 5px 20px rgba(14, 77, 91, 0.1);
                  font-size: 15px;
                  line-height: 140%;
                  color: var(--color-gray2);
                  white-space: nowrap;
                  opacity: 0;
                  visibility: hidden;
                  transition: all .35s ease;
                  z-index: 1;
                }
              }
            }
            .alert{
              @include font-text5;
              color: var(--color-black_70);
              p{
                padding: 0 0 15px;
                &:last-child{
                  padding: 0;
                }
                &.red{
                  color: var(--color-red);
                }
                button{
                  display: inline;
                  padding: 0 0 0 10px;
                  @include font-text7;
                  color: var(--color-black);
                  text-decoration: underline;
                  &:hover{
                    text-decoration: none;
                  }
                }
              }
            }
          }
          .price{
            padding: 0 0 0 40px;
            text-align: right;
            .label{
              @include font-text8;
              color: var(--color-gray2);
              letter-spacing: 1px;
            }
            .num{
              padding: 12px 0 0;
              font-size: 32px;
              font-weight: 500;
              line-height: 140%;
              color: var(--color-black);
              span{
                @include font-text2;
                color: var(--color-gray2);
              }
            }
          }
        }
        .more{
          padding: 40px 0 0;
          button{
            span{
              display: block;
              padding: 0 18px 0 0;
              @include font-text5;
              color: var(--color-black);
            }
            svg{
              display: block;
              max-width: 14px;
              min-width: 14px;
              height: 12px;
              fill: none;
              stroke: var(--color-black);
              transform: rotate(0deg);
              transition: all .2s ease;
            }
            &.active{
              svg{
                transform: rotate(-180deg);
              }
            }
          }
          .hidden{
            display: none;
            ul{
              &.nav{
                margin: 40px 0 0;
                border: 0;
                li{
                  padding: 0 50px 0 0;
                  &:last-child{
                    padding: 0
                  }
                  button{
                    @include font-text5;
                    color: var(--color-gray2);
                    transition: color .2s ease;
                    &:hover{
                      color: var(--color-black);
                    }
                    &.active{
                      color: var(--color-black);
                      text-decoration: underline;
                    }
                  }
                }
              }
              &.tabs{
                li{
                  ol{
                    &.st1{
                      padding: 30px 0 0;
                      li{
                        .label{
                          max-width: 468px;
                          min-width: 468px;
                          span{
                            background: var(--color-additional-text2);
                          }
                        }
                        .desc{
                          background: var(--color-additional-text2);
                        }
                      }
                    }
                    &.st5{
                      width: 530px;
                      padding: 25px 0 0;
                      &.t1{
                        li{
                          grid-template-columns: 140px 140px 140px auto;
                        }
                      }
                      &.t2{
                        li{
                          grid-template-columns: 220px 180px auto;
                        }
                      }
                      li{
                        background: var(--color-gray4_80);
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .tabsinfo{
    padding: 53px 30px 55px;
    ul{
      &.nav{
        li{
          padding: 0 60px 0 0;
          &:last-child{
            padding: 0;
          }
          button{
            padding: 0 0 30px;
            border-bottom: 3px solid var(--color-transparent);
            @include font-menu-title;
            color: var(--color-gray2_50);
            transition: all .2s ease;
            &:hover{
              color: var(--color-black);
            }
            &.active{
              border-color: var(--color-black);
              color: var(--color-black);
            }
          }
        }
      }
      &.tabs{
        ol.accordions{
          padding: 35px 0 0;
          li{
            padding: 0 0 14px;
            &:last-child{
              padding: 0;
            }
            a.file{
              display: inline-flex;
              align-items: center;
              margin: 14px 0 0;
              svg{
                display: block;
                max-width: 13px;
                min-width: 13px;
                height: 13px;
                fill: var(--color-blue);
                stroke: none;
                transform: rotate(0deg)!important;
              }
              span{
                display: block;
                padding: 0 0 0 11px;
                @include font-text10;
                color: var(--color-blue);
                text-decoration: underline;
              }
              &:hover{
                span{
                  text-decoration: none;
                }
              }
            }
            button{
              justify-content: space-between;
              width: 100%;
              padding: 23px 22px;
              background: var(--color-blue_10);
              span{
                display: block;
                text-align: left;
                span{
                  @include font-text10;
                  color: var(--color-blue);
                }
              }
              svg{
                display: block;
                max-width: 16px;
                min-width: 16px;
                height: 16px;
                fill: none;
                stroke: var(--color-blue);
                transform: rotate(0deg);
                transition: all .2s ease;
              }
              &.active{
                svg{
                  transform: rotate(-180deg);
                }
              }
            }
            .hidden{
              display: none;
              .content{
                margin: 5px 0 0;
                padding: 23px 22px;
                background: var(--color-gray3_20);
                @include font-text10;
                color: var(--color-gray1);
                p{
                  padding: 0;
                  margin: 0;
                }
              }
            }
          }
        }
        .plusbtn{
          margin: 50px 0 0;
        }
        .st6{
          margin: 35px 0 0;
        }
        .st7{
          margin: 35px 0 0;
          padding: 22px 25px;
          background: var(--color-blue_10);
          li{
            .label{
              max-width: 250px;
              min-width: 250px;
            }
          }
        }
        .st8{
          margin: 35px 0 0;
        }
      }
    }

  }
}