section.order{
  height: 412px;
  margin: 123px 0 0;
  background: url(../img/bg/bg1.svg) 50% 50% no-repeat;
  background-size: cover;
  &.ext{
    height: 609px;
    background: url(../img/bg/bg2.jpg) 50% 50% no-repeat;
    background-size: cover;
  }
  .info{
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    align-content: center;
    width: 100%;
    height: 100%;
    .title{
      width: 100%;
      padding: 0 0 4px;
      font-weight: 500;
      font-size: 50px;
      line-height: 140%;
      letter-spacing: 2px;
      text-transform: uppercase;
      color: var(--color-button-hover);
    }
    .desc{
      width: 100%;
      padding: 0 0 58px;
      font-weight: 500;
      font-size: 16px;
      line-height: 140%;
      letter-spacing: 2px;
      text-transform: uppercase;
      color: var(--color-button-hover);
    }
  }
  &.ext{
    .info{
      justify-content: flex-end;
      .title{
        text-align: right;
      }
      .desc{
        text-align: right;
      }
    }
  }
}