.newslist{
  &.news{
    padding-top: 212px;
    .title{
      display: flex;
      flex-flow: row wrap;
      justify-content: center;
      strong{
        display: block;
        width: 100%;
        @include font-h2;
        color: var(--color-black);
        text-align: center;
      }
      span{
        position: relative;
        width: 75px;
        height: 4px;
        margin: 10px 0 0;
        background: var(--color-black);
        &:before,
        &:after{
          position: absolute;
          top: 0;
          display: block;
          width: 11px;
          height: 100%;
          background: var(--color-black);
          content: "";
        }
        &:before{
          left: -18px;
        }
        &:after{
          right: -18px;
        }
      }
    }
    ul.list{
      margin: 66px 0 0;
    }
  }
  &.newslist{
    padding-top: 65px;
    ul.list{
      margin: 51px 0 0;
    }
  }
  ul.filters{
    display: flex;
    align-items: center;
    li{
      padding: 0 63px 0 0;
      &:last-child{
        padding: 0
      }
      button{
        position: relative;
        @include font-button-landing;
        color: var(--color-gray6);
        white-space: nowrap;
        &:after{
          position: absolute;
          top: 100%;
          left: 0;
          display: block;
          width: 100%;
          height: 3px;
          margin: 4px 0 0;
          background: var(--color-black);
          opacity: 0;
          transition: opacity .2s ease;
          content: "";
        }
        &:hover,
        &.active{
          color: var(--color-black);
        }
        &.active{
          &:after{
            opacity: 1;
          }
        }
      }
    }
  }
  ul.list{
    border-top: 1px solid var(--color-gray3);
    li{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 46px 0;
      border-bottom: 1px solid var(--color-gray3);
      a{
        display: block;
        &.img{
          max-width: 485px;
          min-width: 485px;
          height: 364px;
          overflow: hidden;
          img{
            display: block;
            width: 100%;
            height: 100%;
            transform: scale(1);
            transition: all .2s ease;
          }
          &:hover{
            img{
              transform: scale(1.1);
            }
          }
        }
        &.info{
          flex: 1 1 auto;
          padding: 0 0 0 55px;
          span{
            display: block;
            &.date{
              padding: 0 0 5px;
              @include font-nexa-landing;
              color: var(--color-gray3)
            }
            &.title{
              padding: 0 0 21px;
              @include font-h2;
              color: var(--color-black)
            }
            &.desc{
              padding: 0 0 40px;
              @include font-nexa-landing;
              color: var(--color-gray1);
            }
            &.morebtn{
              display: flex;
            }
          }
        }
      }
    }
  }
  .more{
    display: flex;
    justify-content: flex-end;
    padding: 40px 0 0;
  }
}