header.main{
  position: relative;
  width: 100%;
  height: 90px;
  z-index: 100;
  .wrap{
    display: flex;
    align-items: center;
    height: 100%;
    a.logo{
      display: flex;
      align-items: center;
      height: 100%;
      font-weight: 700;
      font-size: 19px;
      line-height: 122%;
      letter-spacing: 1px;
      text-transform: uppercase;
      color: var(--color-black);
    }
    ul{
      display: flex;
      align-items: center;
      height: 100%;
      li{
        display: flex;
        align-items: center;
        height: 100%;
        a{
          display: flex;
          align-items: center;
          height: 100%;
          @include font-button-landing;
          white-space: nowrap;
          transition: color .2s ease;
        }
      }
      &.nav{
        padding: 0 0 0 64px;
        li{
          margin: 0 60px 0 0;
          &:last-child{
            margin: 0
          }
          a{
            color: var(--color-gray1);
            &:hover{
              color: var(--color-black);
            }
          }
          &.submenu{
            position: relative;
            ul{
              position: absolute;
              top: calc(50% + 30px);
              left: 0;
              display: block;
              height: auto;
              margin: 0;
              background: var(--color-white);
              box-shadow: 5px 7px 20px 4px rgba(14, 77, 91, 0.15);
              visibility: hidden;
              opacity: 0;
              transition: all .2s ease;
              li{
                position: relative;
                display: block;
                width: 100%;
                height: auto;
                a{
                  display: flex;
                  align-items: center;
                  height: 40px;
                  padding: 0 28px 0 30px;
                  background: var(--color-white);
                  @include font-menu-title;
                  transition: background .2s ease;
                  &:hover{
                    background: var(--color-gray4);
                  }
                }
                &:hover{
                  a{
                    background: var(--color-gray4);
                  }
                }
                ul{
                  top: 0;
                  left: 100%;
                  background: var(--color-gray4);
                  box-shadow: none;
                  li{
                    a{
                      transition: color .2s ease;
                      &:hover{
                        color: var(--color-gray2);
                      }
                    }
                  }
                }
              }
            }
            &:hover > ul{
              visibility: visible;
              opacity: 1;
            }
          }
        }
      }
      &.ui{
        margin: 0 0 0 auto;
        li{
          margin: 0 42px 0 0;
          &:last-child{
            margin: 0;
          }
          a{
            color: var(--color-black);
            &:hover{
              color: var(--color-gray1);
            }
          }
          &.notify{
            position: relative;
            button.trigger{
              position: relative;
              &.active{
                &:after{
                  position: absolute;
                  top: -2px;
                  right: -2px;
                  display: block;
                  width: 6px;
                  height: 6px;
                  border: 2px solid var(--color-white);
                  border-radius: 50%;
                  background: var(--color-orange);
                  content: "";
                }
              }
              svg{
                display: block;
                width: 19px;
                height: 21px;
              }
            }
            .hidden{
              position: absolute;
              top: calc(50% + 30px);
              right: -10px;
              width: 318px;
              background: var(--color-white);
              box-shadow: 5px 7px 20px 4px rgba(14, 77, 91, 0.15);
              visibility: hidden;
              opacity: 0;
              transition: all .2s ease;
              &.active{
                visibility: visible;
                opacity: 1;
              }
              .titlebox{
                position: relative;
                display: flex;
                align-items: center;
                height: 58px;
                padding: 0 22px;
                border-bottom: 1px solid var(--color-gray3);
                span{
                  @include font-menu-title;
                  color: var(--color-black);
                }
                button{
                  position: absolute;
                  top: 14px;
                  right: 14px;
                  width: 12px;
                  height: 12px;
                  margin: 0 0 0 auto;
                  svg{
                    display: block;
                    width: 100%;
                    height: 100%;
                    fill: var(--color-gray3);
                  }
                }
              }
              .box{
                position: relative;
                max-height: 366px;
                ul{
                  display: block;
                  li{
                    width: 100%;
                    padding: 0;
                    button{
                      position: relative;
                      display: block;
                      width: 100%;
                      padding: 24px 36px 24px 20px;
                      border-bottom: 1px solid var(--color-gray3);
                      background: var(--color-card-plus);
                      text-align: left;
                      .title{
                        padding: 0 0 8px;
                        @include font-text6;
                        color: var(--color-black);
                      }
                      .desc{
                        padding: 0 0 10px;
                        @include font-text10;
                        color: var(--color-button-hover);
                      }
                      .status{
                        @include font-text10;
                        span{
                          color: var(--color-gray2);
                          &:last-child{
                            color: var(--color-black);
                          }
                        }
                      }
                      .close{
                        position: absolute;
                        top: 14px;
                        right: 14px;
                        width: 12px;
                        height: 12px;
                        svg{
                          display: block;
                          width: 100%;
                          height: 100%;
                          fill: var(--color-gray3);
                        }
                      }
                    }
                  }
                }
                .ps__rail-x{
                  display: none;
                }
                .ps__rail-y{
                  right: 0;
                  width: 3px;
                  background: var(--color-transparent);
                  opacity: 1;
                  .ps__thumb-y{
                    right: 0;
                    width: 100%;
                    border-radius: 0;
                    background: var(--color-black);
                    opacity: 1;
                  }
                }
              }
            }
          }
          &.time{
            display: flex;
            align-items: center;
            svg{
              display: block;
              max-width: 21px;
              min-width: 21px;
              height: 21px;
            }
            span{
              display: block;
              padding: 0 0 0 10px;
              @include font-text6;
            }
          }
          &.profile{
            position: relative;
            button{
              position: relative;
              svg{
                display: block;
                width: 21px;
                height: 21px;
              }
              &.active{
                &:after{
                  position: absolute;
                  top: -2px;
                  right: -2px;
                  display: block;
                  width: 6px;
                  height: 6px;
                  border: 2px solid var(--color-white);
                  border-radius: 50%;
                  background: var(--color-orange);
                  content: "";
                }
              }
            }
            .hidden{
              position: absolute;
              top: calc(50% + 30px);
              right: -10px;
              width: 318px;
              background: var(--color-white);
              box-shadow: 5px 7px 20px 4px rgba(14, 77, 91, 0.15);
              visibility: hidden;
              opacity: 0;
              transition: all .2s ease;
              &.active{
                visibility: visible;
                opacity: 1;
              }
              .userinfo{
                position: relative;
                display: flex;
                align-items: center;
                padding: 24px 20px 20px;
                border-bottom: 1px solid var(--color-gray3);
                background: var(--color-card-plus);
                .ava{
                  max-width: 35px;
                  min-width: 35px;
                  height: 35px;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  border: 1px solid var(--color-green-plus);
                  border-radius: 50%;
                  overflow: hidden;
                  img{
                    display: block;
                    width: 100%;
                  }
                }
                .info{
                  flex: 1 1 auto;
                  padding: 0 0 0 14px;
                  .name{
                    padding: 0 0 4px;
                    font-weight: 600;
                    font-size: 13px;
                    line-height: 140%;
                  }
                  .desc{
                    font-size: 10px;
                    line-height: 140%;
                    color: var(--color-gray2_70);
                  }
                }
                button{
                  position: absolute;
                  top: 14px;
                  right: 14px;
                  width: 12px;
                  height: 12px;
                  margin: 0 0 0 auto;
                  svg{
                    display: block;
                    width: 100%;
                    height: 100%;
                    fill: var(--color-gray3);
                  }
                }
              }
              ul.data{
                display: block;
                height: auto;
                padding: 30px 25px;
                li{
                  display: block;
                  height: auto;
                  padding: 0 0 35px;
                  margin: 0;
                  &:last-child{
                    padding: 0;
                  }
                  a{
                    display: flex;
                    align-items: center;
                    height: auto;
                    .ico{
                      position: relative;
                      max-width: 18px;
                      min-width: 18px;
                      &.home{
                        height: 17px;
                      }
                      &.mail{
                        height: 12px;
                      }
                      &.settings{
                        height: 19px;
                      }
                      &.logout{
                        height: 14px;
                      }
                      &.active{
                        &:after{
                          position: absolute;
                          top: -2px;
                          right: -2px;
                          display: block;
                          width: 6px;
                          height: 6px;
                          border: 2px solid var(--color-white);
                          border-radius: 50%;
                          background: var(--color-orange);
                          content: "";
                        }
                      }
                      svg{
                        display: block;
                        width: 100%;
                        height: 100%;
                      }
                    }
                    span{
                      display: block;
                      padding: 0 0 0 14px;
                      @include font-menu-title;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}