section.cardlist{
  .info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 58px 0 0;
    .result{
      @include font-text7;
      color: var(--color-gray2);
      span{
        color: var(--color-black);
      }
    }
    .sort{
      position: relative;
      button.toggle{
        span{
          display: block;
          padding: 0 12px 0 0;
          @include font-text7;
          color: var(--color-black);
        }
        svg{
          display: block;
          max-width: 18px;
          min-width: 18px;
          height: 18px;
          fill: none;
          stroke: var(--color-black);
          stroke-width: 2;
          transform: rotate(0deg);
          transition: all .2s ease;
        }
        &.active{
          svg{
            transform: rotate(-180deg);
          }
        }
      }
      ul{
        position: absolute;
        top: 100%;
        right: 0;
        display: none;
        min-width: 100%;
        margin: 20px 0 0;
        box-shadow: 4px 4px 10px rgba(0,0,0,.1);
        &:before,
        &:after{
          position: absolute;
          display: block;
          z-index: 1;
          content: "";
        }
        &:before{
          top: -6px;
          left: 20px;
          width: 20px;
          height: 20px;
          background: var(--color-white);
          transform: rotate(-45deg);
          box-shadow: 0 0 9px rgba(0, 0, 0, .1);
        }
        &:after{
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: var(--color-white);
        }
        li{
          position: relative;
          z-index: 2;
          button{
            justify-content: flex-start;
            width: 100%;
            padding: 4px 10px;
            background: var(--color-white);
            @include font-text7;
            color: var(--color-black);
            white-space: nowrap;
            transition: background .2s ease;
            &:hover{
              background: var(--color-gray5);
            }
          }
        }
      }
    }
  }
  ul.result{
    padding: 55px 0 0;
    li{
      padding: 40px 40px 40px 62px;
      margin: 0 0 35px;
      background: var(--color-card-plus);
      &:last-child{
        margin: 0;
      }
      .label{
        color: var(--color-gray3);
      }
      .top{
        display: flex;
        align-items: center;
        .number{
          position: relative;
          span{
            @include font-text5;
            color: var(--color-gray2);
            &::last-child{
              padding: 0 0 0 5px;
            }
            &.label{
              position: absolute;
              top: 50%;
              left: -24px;
              display: block;
              width: 14px;
              height: 14px;
              margin: -7px 0 0;
              border-radius: 50%;
              background: var(--color-green-plus);
              span{
                position: absolute;
                bottom: calc(100% + 10px);
                left: 0;
                padding: 13px 18px;
                border-radius: 0 5px 5px 0;
                background: var(--color-white);
                font-size: 13px;
                font-weight: 500;
                line-height: 140%;
                color: var(--color-red);
                white-space: nowrap;
                box-shadow: 5px 5px 20px rgba(14, 77, 91, 0.1);
                opacity: 0;
                visibility: hidden;
                transition: all .2s ease;
              }
              &:hover{
                span{
                  opacity: 1;
                  visibility: visible;
                }
              }
            }
          }
          &.inactive{
            span.label{
              background: var(--color-red);
              cursor: pointer;
            }
          }
        }
        .status{
          display: flex;
          align-items: center;
          padding: 0 0 0 30px;
          svg{
            display: block;
            max-width: 12px;
            min-width: 12px;
            height: 16px;
            fill: var(--color-blue-plus);
          }
          span{
            display: block;
            padding: 0 0 0 10px;
            @include font-text3;
            color: var(--color-blue-plus);
          }
          &.inactive{
            svg{
              fill: var(--color-red);
            }
            span{
              color: var(--color-red);
            }
          }
        }
      }
      .middle{
        display: flex;
        justify-content: space-between;
        padding: 15px 0 0;
        a.name{
          flex: 1 1 auto;
          display: block;
          @include font-h4;
          color: var(--color-black);
          text-decoration: underline;
          &:hover{
            text-decoration: none;
          }
        }
        .price{
          max-width: 245px;
          min-width: 245px;
          margin: 0 0 0 100px;
          .label{
            margin: -34px 0 0;
            padding: 0px 0 6px;
            text-align: right;
          }
          .num{
            @include font-text1;
            color: var(--color-black);
            text-align: right;
            span{
              @include font-text2;
              color: var(--color-gray2);
            }
          }
        }
      }
      .bottom{
        display: flex;
        justify-content: space-between;
        padding: 34px 0 0;
        ol{
          display: block;
          li{
            padding: 0;
          }
          &.st1{
            flex: 1 1 auto;
            li{
              .label{
                max-width: 220px;
                min-width: 220px;
                span{
                  background: var(--color-card-plus);
                }
              }
              .desc{
                background: var(--color-card-plus);
              }
            }
          }
          &.st2{
            max-width: 245px;
            min-width: 245px;
            margin: 0 0 0 100px;
            li{
              text-align: right;
              white-space: nowrap;
              margin: 0 0 19px;
              .label{
                padding: 0 0 7px
              }
              .desc{
                color: var(--color-black);
              }
            }
          }
        }
      }
      .actions{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 34px 0 0;
        .signature{
          display: flex;
          align-items: center;
          svg{
            display: block;
            max-width: 16px;
            min-width: 16px;
            height: 16px;
            fill: var(--color-black);
          }
          span{
            display: block;
            padding: 0 0 0 11px;
            @include font-text3;
            color: var(--color-black);
            text-transform: uppercase;
          }
        }
        button{
          max-width: 245px;
          min-width: 245px;
          height: 59px;
        }
      }
    }
  }
}
