form.registration{
  padding-top: 58px;
  padding-bottom: 115px;
  ul{
    &.nav{
      position: relative;
      justify-content: space-between;
      width: 100%;
      &:after{
        position: absolute;
        bottom: -3px;
        left: 0;
        display: block;
        width: 100%;
        height: 3px;
        background: var(--color-gray4);
        content: "";
      }
      li{
        button{
          position: relative;
          padding: 0 0 30px;
          border-bottom: 3px solid var(--color-transparent);
          @include font-menu-title;
          color: var(--color-gray3);
          transition: all .35s ease;
          z-index: 1;
          &:hover,
          &.active{
            color: var(--color-black);
          }
          &.active{
            border-color: var(--color-black);
          }
        }
      }
    }
    &.tabs{
      margin: 54px 0 0;
      li{
        fieldset{
          max-width: 788px;
        }
        input{
          background: var(--color-gray4);
        }
        .btn{
          width: 245px;
          height: 59px;
        }
        .rules{
          padding: 0 0 44px;
          @include font-text5;
          line-height: 175%;
          color: var(--color-gray1);
          p{
            padding: 0;
            margin: 0;
          }
        }
        .checkbox{
          padding: 0 0 32px;
          label{
            padding: 0;
            &:before{
              background: var(--color-gray9);
            }
          }
        }
        .subtitle{
          padding: 0 0 48px;
        }
        .quote{
          position: relative;
          padding: 2px 0 2px 28px;
          @include font-text5;
          line-height: 175%;
          color: var(--color-gray1);
          &:before{
            position: absolute;
            top: 0;
            left: 0;
            display: block;
            width: 3px;
            height: 100%;
            background: var(--color-black);
            content: "";
          }
        }
        .hidden{
          display: none;
        }
        .placements{
          display: none;
          padding: 0 0 58px;
          .subtitle{
            padding: 22px 0 24px;
          }
          ul{
            display: flex;
            background: var(--color-gray8);
            margin: 0 0 10px;
            &:last-child{
              margin: 0;
            }
            li{
              @include font-text10;
              &:nth-child(1) {
                max-width: 110px;
                min-width: 110px;
              }
              &:nth-child(2) {
                max-width: 200px;
                min-width: 200px;
              }
              &:nth-child(3) {
                flex: 1 1 auto;
              }
              &:nth-child(4) {
                max-width: 50px;
                min-width: 50px;
              }
            }
            &.head{
              li{
                padding: 24px;
                color: var(--color-gray2);
              }
            }
            &.body{
              li{
                input{
                  width: calc(100% - 12px);
                  height: 40px;
                  padding: 0 12px;
                  margin: 12px 0 12px 12px;
                  border: 1px solid var(--color-gray6);
                  background: var(--color-white);
                  transition: all .35s ease;
                  &[readonly]{
                    border-color: var(--color-gray8);
                    background: var(--color-gray4);
                  }
                }
                button{
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  width: 100%;
                  height: 100%;
                  svg{
                    display: block;
                    max-width: 3px;
                    min-width: 3px;
                    height: 15px;
                  }
                }
                .hidden{
                  position: absolute;
                  top: 50px;
                  right: 24px;
                  display: none;
                  width: 294px;
                  padding: 21px 24px 24px;
                  background: var(--color-white);
                  box-shadow: 5px 5px 20px 4px rgba(14, 77, 91, 0.1);
                  z-index: 1;
                  &.open{
                    display: block;
                  }
                  button{
                    display: block;
                    margin: 0 0 10px;
                    @include font-text5;
                    color: var(--color-black);
                    text-align: left;
                    &:hover{
                      color: var(--color-red);
                    }
                    &:last-child{
                      margin: 0;
                      color: var(--color-red);
                      &:hover{
                        color: var(--color-black);
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .ready{
    max-width: 788px;
    .subtitle{
      padding: 0 0 40px;
    }
    fieldset{
      padding: 0 0 48px;
      .select2{
        span.selection{
          .select2-selection{
            background: var(--color-gray4);
          }
        }
      }
    }
  }
}