.swiper{
  position: relative;
  &.slider_screen{
    padding: 0 470px;
    .swiper-wrapper{
      .swiper-slide{
        position: relative;
        width: 1540px;
        height: 610px;
        img{
          display: block;
          width: 100%;
          height: 100%;
        }
        .info{
          position: absolute;
          top: 0;
          left: 0;
          display: flex;
          align-items: center;
          align-content: center;
          flex-flow: row wrap;
          width: 100%;
          height: 100%;
          padding: 0 0 0 204px;
          .label{
            width: 100%;
            padding: 0 0 18px;
            font-weight: 500;
            font-size: 14px;
            line-height: 122%;
            letter-spacing: 0.8px;
            text-transform: uppercase;
            color: var(--color-additional-text);
          }
          .title{
            width: 100%;
            font-weight: 500;
            font-size: 49px;
            line-height: 110%;
            letter-spacing: 1px;
            text-transform: uppercase;
            color: var(--color-screen-swiper);
          }
          .desc{
            width: 100%;
            padding: 18px 0 0 60px;
            font-weight: 300;
            font-size: 40px;
            line-height: 110%;
            letter-spacing: 0.5px;
            text-transform: uppercase;
            color: var(--color-screen-swiper);
          }
          .btn{
            margin: 60px 0 0;
          }
        }
      }
    }
  }
  &.slider_carousel{
    padding: 96px 470px 0;
    .swiper-wrapper{
      .swiper-slide{
        position: relative;
        width: 504px;
        .num{
          padding: 0 0 48px;
          font-weight: 500;
          font-size: 32px;
          line-height: 110%;
          letter-spacing: 0.5px;
          text-transform: uppercase;
          color: var(--color-gray3);
          transition: color .2s ease;
        }
        a{
          display: block;
          width: 100%;
          .img{
            width: 100%;
            height: 432px;
            overflow: hidden;
            img{
              display: block;
              width: 100%;
              height: 100%;
              transform: scale(1);
              transition: all .2s ease;
            }
          }
          .title{
            padding: 32px 18px 0;
            @include font-h1-landing;
            color: var(--color-black);
          }
          .desc{
            padding: 22px 18px 0;
            @include font-nexa-landing;
            color: var(--color-gray2);
            transition: color .2s ease;
          }
          .morebtn{
            padding: 36px 18px 0;
            span{
              opacity: 0;
              transition: opacity .2s ease;
            }
            svg{
              margin: 0;
              opacity: 0;
              transition: all .2s ease;
            }
          }
        }
        &:hover{
          .num{
            color: var(--color-black);
          }
          a{
            .img{
              img{
                transform: scale(1.1);
              }
            }
            .morebtn{
              span{
                opacity: 1;
              }
              svg{
                margin: 0 0 0 14px;
                opacity: 1;
              }
            }
          }
        }
      }
    }
    .navigation{
      margin: 33px 0 0;
    }
  }
  .navigation{
    position: absolute;
    top: 50%;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 0;
    padding: 0 516px;
    z-index: 1;
    div{
      position: relative;
      max-width: 52px;
      min-width: 52px;
      height: 52px;
      cursor: pointer;
      &.prev{
        img{
          transform: rotate(-180deg);
        }
      }
      img{
        position: absolute;
        display: block;
        height: 100%;
        transition: opacity .2s ease;
        &:first-child{
          opacity: 1;
        }
        &:last-child{
          opacity: 0;
        }
      }
      &:hover{
        img{
          &:first-child{
            opacity: 0;
          }
          &:last-child{
            opacity: 1;
          }
        }
      }
    }
  }
  .swiper-notification{
    display: none;
  }
}