.modalbox{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100Vh;
  background: var(--color-black_50);
  visibility: hidden;
  opacity: 0;
  transition: all .2s ease;
  z-index: -1;
  &.active{
    visibility: visible;
    opacity: 1;
    z-index: 1000;
  }
  .modalcontent{
    position: absolute;
    top: 0;
    right: -1000px;
    width: 950px;
    height: 100Vh;
    background: var(--color-white);
    transition: right .2s ease;
    &.open{
      right: 0;
    }
    &.st1{
      padding: 150px 80px 0;
      .more{
        padding: 10px 0 54px;
        a{
          @include font-text5;
          color: var(--color-gray2);
          text-decoration: underline;
          &:hover{
            text-decoration: none;
          }
        }
      }
      fieldset.pt{
        padding: 10px 0 80px;
        label{
          padding: 0 0 6px;
          @include font-text6;
          font-weight: 600;
        }
      }
    }
    &.st2{
      padding: 340px 0 0;
      .title{
        padding: 0 0 26px;
        text-align: center;
      }
      .desc{
        padding: 0 0 80px;
        @include font-h5;
        text-align: center;
      }
      button{
        margin: 0 auto;
      }
    }
    &.st3{
      width: 1130px;
      padding: 150px 82px 0;
      .title{
        padding: 0 0 40px;
      }
      ul.list{
        width: 568px;
        li{
          width: 100%;
          padding: 0 0 40px;
          &:last-child{
            padding: 0;
          }
          .name{
            padding: 0 0 20px;
            @include font-menu-title;
            color: var(--color-gray1);
          }
          .item{
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            padding: 17px 16px 16px;
            margin: 0 0 5px;
            background: var(--color-gray4);
            &:last-child{
              margin: 0;
            }
            .label{
              @include font-text4;
              color: var(--color-black_60);
            }
            .desc{
              @include font-text5;
            }
          }
        }
      }
    }
    &.st4{
      width: 1130px;
      padding: 150px 82px 0;
      .title{
        padding: 0 0 40px;
      }
      .tabbox{
        ul{
          &.nav{
            li{
              padding: 0 50px 0 0;
              &:last-child{
                padding: 0;
              }
              button{
                border: 0 solid var(--color-transparent);
                border-width: 0 0 1px;
                @include font-text5;
                color: var(--color-gray2);
                &.active{
                  border-color: var(--color-black);
                  color: var(--color-black);
                }
              }
            }
          }
          &.tabs{
            margin: 26px 0 0;
            li{
              ol{
                li{
                  grid-template-columns: 60px 520px 160px 160px auto;
                  margin: 0 0 5px;
                  background: var(--color-gray4_80);
                  &:last-child{
                    margin: 0;
                  }
                }
              }
            }
          }
        }
      }
    }
    button.close{
      position: absolute;
      top: 43px;
      right: 36px;
      width: 16px;
      height: 16px;
      svg{
        display: block;
        width: 100%;
        height: 100%;
        stroke: var(--color-black);
      }
    }
    .title{
      padding: 0 0 50px;
      @include font-h2;
      color: var(--color-black);
    }
    form{
      width: 100%;
      fieldset{
        width: 100%;
        padding: 0 0 25px;
        margin: 0;
        border: 0;
        textarea{
          max-width: 100%;
          min-width: 100%;
          max-height: 128px;
          min-height: 128px;
          padding: 23px 22px;
          background: var(--color-gray8);
          @include font-text10;
          color: var(--color-black);
          &::placeholder{
            color: var(--color-gray3);
          }
        }
        .filename{
          display: none;
          padding: 0 0 10px;
          @include font-text6;
          color: var(--color-black);
          &.active{
            display: block;
          }
        }
        input[type="file"]{
          display: block;
          width: 0;
          height: 0;
          visibility: hidden;
        }
        button.file{
          justify-content: flex-start;
          width: 312px;
          height: 59px;
          padding: 0 23px;
          background: var(--color-gray4);
          transition: background .2s ease;
          svg{
            display: block;
            max-width: 24px;
            min-width: 24px;
            height: 24px;
            fill: var(--color-black);
            transition: fill .2s ease;
          }
          span{
            display: block;
            padding: 0 0 0 17px;
            @include font-text6;
            color: var(--color-black);
            transition: color .2s ease;
          }
          &:hover{
            background: var(--color-button-hover);
            svg{
              fill: var(--color-white);
            }
            span{
              color: var(--color-white);
            }
          }
        }
      }
      .errors{
        padding: 12px 0 0;
        p{
          padding: 0;
          margin: 0;
          @include font-text5;
          color: var(--color-red);
        }
      }
      button[type="submit"]{
        width: 245px;
        height: 59px;
        margin: 80px 0 0;
      }
    }
  }
  &.active{
    visibility: visible;
    opacity: 1;
    z-index: 1000;
  }
  &.alertbox{
    display: flex;
    justify-content: center;
    align-items: center;
    .alertcontent{
      position: relative;
      top: -100px;
      right: 0;
      width: 584px;
      height: auto;
      padding: 68px 0 64px;
      background: var(--color-white);
      transition: all .2s ease;
      opacity: 0;
      visibility: hidden;
      &.open{
        top: 0;
        opacity: 1;
        visibility: visible;
      }
      button.close{
        position: absolute;
        top: 43px;
        right: 36px;
        width: 16px;
        height: 16px;
        svg{
          display: block;
          width: 100%;
          height: 100%;
          stroke: var(--color-black);
        }
      }
      .title{
        padding: 0;
        @include font-text5;
        text-transform: none;
        text-align: center;
        letter-spacing: 0;
      }
    }
  }
}