section.about{
  padding-top: 104px;
  ul{
    position: relative;
    display: flex;
    justify-content: center;
    width: calc(100% + 150px);
    margin: 0 0 0 -75px;
    &:before{
      position: absolute;
      top: 50%;
      left: 0;
      display: block;
      width: 2px;
      height: 116px;
      margin: -58px 0 0;
      background: var(--color-gray5);
      content: "";
    }
    li{
      position: relative;
      display: flex;
      flex-flow: row wrap;
      justify-content: center;
      padding: 0 75px;
      &:after{
        position: absolute;
        top: 50%;
        left: 0;
        display: block;
        width: 2px;
        height: 116px;
        margin: -58px 0 0;
        background: var(--color-gray5);
        content: "";
      }
      .img{
        display: flex;
        justify-content: center;
        align-items: flex-end;
        width: 100%;
        img{
          display: block;
        }
      }
      .title{
        padding: 32px 0 0;
        font-weight: 700;
        font-size: 21px;
        line-height: 140%;
        letter-spacing: 2px;
        text-transform: uppercase;
        color: var(--color-gray1);
      }
      .desc{
        padding: 18px 0 0;
        @include font-nexa-landing;
        color: var(--color-gray2);
        text-align: center;
      }
    }
  }
  .more{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 60px 0 0;
  }
}