section.senders{
  padding: 71px 0 84px;
  border-top: 1px solid var(--color-gray3);
  background: var(--color-gray7);
  .wrap{
    display: flex;
    align-items: center;
    .title{
      font-weight: 700;
      font-size: 19px;
      line-height: 140%;
      letter-spacing: 2px;
      text-transform: uppercase;
      color: var(--color-black);
      white-space: nowrap;
    }
    form{
      display: flex;
      align-items: center;
      height: 59px;
      margin: 0 0 0 auto;
      input{
        display: block;
        max-width: 380px;
        min-width: 380px;
        height: 100%;
        margin: 0 29px 0 0;
        padding: 0 10px;
        border: 0 solid var(--color-gray2);
        border-width: 0 0 1px;
        background: var(--color-transparent);
        font-family: 'NexaLight';
        font-size: 15px;
        line-height: 185%;
        letter-spacing: 0.015em;
        color: var(--color-gray1);
      }
      button{
        max-width: 192px;
        min-width: 192px;
        height: 100%;
        border: 2px solid var(--color-black);
        border-radius: 30px;
        @include font-button-landing;
        color: var(--color-black);
        transition: all .2s ease;
        &:hover{
          background: var(--color-black);
          color: var(--color-white);
        }
      }
    }
  }
}